import { home } from '@api';
const { selectArticlesById } = home;

export default {
  data() {
    return {
      articleInfo: {
        title: '',
        userName: '',
        summary: ''
      }
    };
  },
  async onLoad(e) {
    // console.info(e)
    const { articleInfo } = await selectArticlesById({ id: e.id });
    this.articleInfo = articleInfo || {};
    // console.info(articleInfo)
  }
};
